* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

body {
    width: 100vw;
    min-height: 100vh;
    background-image: url(../images/bg.png);
    background-repeat: repeat-y;
    background-size: cover;
    overflow-x: hidden;
}

a:active,
a:focus {
    outline: none;
}

.container {
    max-width: 1480px;
    margin: 0 auto;
    padding: 0 15px;
}