@import "fonts";
@import "vars";
@import "global";
@import "media";

.header {
    &__inner {
        padding-top: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__btn {
        width: 109px;
        height: 40px;
        background: #844DBC;
        box-shadow: 0px 0px 25px rgba(132, 77, 188, 0.4);
        border-radius: 67px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
            transition: all .5s;
            box-shadow: 0px 0px 50px rgba(132, 77, 188, 0.8);
        }

        &__text {
            font-family: Roboto;
            font-weight: bold;
            font-size: 14px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: #FFFFFF;
            padding: 11px 30px;
        }
    }

    &__nav {
        display: flex;
        flex-direction: row;

        &__item {
            position: relative;
            color: #FFFFFF;
            font-family: Roboto;
            font-weight: bold;
            font-size: 18px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            margin: 0 25px 0 25px;

            &:hover {
                color: #844dbc;
            }
        }
    }

    &__logo {
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        font-size: 36px;
        text-transform: uppercase;
        color: #FFFFFF;

        &__span {
            color: #844DBC;
        }

    }
}


.main {

    &__info {
        margin-top: 250px;

        &__btn {
            width: 172px;
            height: 60px;
            background: #844DBC;
            box-shadow: 0px 0px 25px rgba(132, 77, 188, 0.4);
            border-radius: 67px;
            display: flex;
            align-items: center;
            justify-content: center;

            &:hover {
                transition: all .5s;
                box-shadow: 0px 0px 50px rgba(132, 77, 188, 0.8);
            }

            &__text {
                font-family: Roboto;
                font-weight: bold;
                font-size: 18px;
                padding: 18px 50px;
                letter-spacing: 0.02em;
                text-transform: uppercase;
                color: #FFFFFF;
            }
        }
    }


    &__title {
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        font-size: 64px;
        letter-spacing: -0.035em;
        text-transform: capitalize;
        color: #E4E7EF;
        max-width: 780px;
    }

    &__text {
        width: 480px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 300;
        font-size: 18px;
        line-height: 24px;
        color: #DDE0EA;
        margin: 25px 0 40px 0;
    }

    &__human {
        position: absolute;
        max-width: 750px;
        right: 220px;
        bottom: 0;
    }
}

.news {
    &__items {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        margin-top: 70px;
    }

    &__item {
        max-width: 1024px;
        height: 140px;
        display: flex;
        margin: 25px auto 25px;
        padding: 0;
        background-color: rgba(62, 62, 62, 0.3);

        &__img {
            max-width: 240px;
            max-height: 140px;
        }

        &__info {
            margin: 25px 30px 23px 30px;
        }

        &__title {
            max-width: 740px;
            font-family: Roboto;
            font-weight: bold;
            font-size: 24px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: #FFFFFF;
        }

        &__text {
            max-width: 740px;
            font-family: Montserrat;
            font-style: normal;
            font-weight: 300;
            font-size: 18px;
            line-height: 24px;
            color: #DDE0EA;
            margin-top: 12px;
        }
    }
}

.login {
    &__reg {
        display: flex;
        flex-direction: column;
        margin-top: 220px;
        max-width: 416px;
    }

    &__form {
        flex-direction: column;
    }

    &__inner {
        display: flex;

        justify-content: center;
    }

    &__title {
        font-family: Roboto;
        font-weight: bold;
        font-size: 36px;
        line-height: 42px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #FFFFFF;
        text-align: center;
    }

    &__text {
        font-family: Montserrat;
        font-weight: 300;
        font-size: 18px;
        line-height: 24px;
        color: #DDE0EA;
        text-align: center;
        margin: 15px 0 25px;
    }

    &__input-email {
        width: 100%;
        width: 416px;
        height: 60px;
        background-color: #26252E;
        border: none;
        border-radius: 40px;

        font-family: Montserrat;
        font-weight: 300;
        font-size: 18px;
        line-height: 24px;
        color: #FFFFFF;
        padding-left: 30px;
        margin-bottom: 30px;
    }

    &__input-password {
        width: 416px;
        height: 60px;
        background-color: #26252E;
        border: none;
        border-radius: 40px;

        font-family: Montserrat;
        font-weight: 300;
        font-size: 18px;
        line-height: 24px;
        color: #FFFFFF;
        padding-left: 30px;
        margin-bottom: 30px;
    }

    &__btn {
        width: 100%;
        width: 416px;
        height: 60px;
        background: #844DBC;
        box-shadow: 0px 0px 35px rgba(132, 77, 188, 0.15);
        border-radius: 72px;
        border: none;

        font-family: Montserrat;
        font-weight: 600;
        font-size: 18px;
        text-transform: capitalize;
        color: #E4E7EF;
    }

    &__info {
        font-family: Montserrat;
        font-size: 18px;
        color: #DDE0EA;
        text-align: center;
        margin: 30px 0 15px;
    }

    &__link {
        font-family: Montserrat;
        font-weight: 700;
        font-size: 18px;
        color: #844DBC;
    }

    &__create {
        text-align: center;
    }
}

.item {
    margin-top: 60px;

    &__inner {
        max-width: 1230px;
    }

    &__text {
        &__title {
            max-width: 1230px;
            margin: 30px 0 30px;
            font-family: Montserrat;
            font-weight: 500;
            font-size: 36px;
            line-height: 131.9%;
            color: #FFFFFF;
        }

        &__img {
            padding: 15px 0 15px;
            max-width: 1000px;
            margin: 0 auto;
            border-radius: 20px;
        }

        &__normal {
            max-width: 1230px;
            margin: 20px 0 20px;
            font-family: Montserrat;
            font-weight: 500;
            font-size: 24px;
            line-height: 131.9%;
            color: #FFFFFF;
        }
    }

    &__banner {
        display: flex;

        &__title {
            margin: 30px 0 0 30px;
            max-width: 720px;
            font-family: Montserrat;
            font-weight: 500;
            font-size: 36px;
            line-height: 44px;
            letter-spacing: -0.035em;
            text-transform: capitalize;
            color: #282828;
        }

        &__fon {
            width: 883px;
            height: 223px;

        }

        &__img {
            width: 350px;
            height: 223px;
        }
    }
}

.course {
    &__title {
        margin: 80px 0;
        text-align: center;
        font-family: Montserrat;
        font-weight: bold;
        font-size: 48px;
        line-height: 59px;
        letter-spacing: -0.035em;
        color: #FFFFFF;

        &__span {
            color: #844dbc;
        }
    }

    &__items {
        display: flex;
        justify-content: space-around;
    }

    &__item {
        position: relative;
        width: 354px;
        height: 354px;
        background: linear-gradient(143.14deg, rgba(188, 80, 239, 0.3) -15.34%, rgba(105, 105, 105, 0) 105.6%, rgba(188, 80, 239, 0.12) 105.6%);
        border-radius: 40px;

        &__img {
            margin: 55px 117px;

        }

        &__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 217px;
            height: 38px;
            border: 2px solid rgba(255, 255, 255, 0.4);
            border-radius: 51px;

            &__title {
                font-family: Roboto;
                font-weight: 700;
                font-size: 16px;
                line-height: 131.9%;
                letter-spacing: 0.02em;
                text-transform: uppercase;
                color: rgba(255, 255, 255, 0.4);
            }
        }

        &__form {
            position: absolute;
            bottom: 35px;
            right: 68px;
            width: 217px;
        }

        &__title {
            font-family: Roboto;
            font-weight: 700;
            font-size: 24px;
            line-height: 131.9%;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 35px;
        }
    }
}

.guide {

    &__inner {}

    &__items {
        margin-top: 60px;
        margin-bottom: 60px;
    }

    &__item {}

    &__title {
        text-align: center;
        margin: 40px 0;
        font-family: Roboto;
        font-weight: 700;
        font-size: 36px;
        line-height: 42px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #FFFFFF;
    }
}

.container {}